<template>
    
    <div>
        <button type="button" class="btn fw-bold btn-sm btn-success d-flex align-items-center" @click="dialogVisible = true"><i class="bi bi-person-plus fs-4"></i><span class="ms-1 lh-1">EKLE</span></button>

        <Dialog v-model:visible="dialogVisible" modal header="Yeni Ürün Segmenti Ekle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="create">

                <div class="d-flex flex-column mb-5">
                    <label class="form-label">İsim</label>
                    <span class="p-input-icon-left">
                        <i class="bi bi-person" />
                        <InputText v-model="form.name" class="w-100" />
                    </span>
                </div>

                <Divider />

                <div class="row align-items-center">
                    <label class="col-md-6 form-label">Sayfa İçerik Listeleme</label>
                    <div class="col-md-6">
                        <Dropdown v-model="form.segment_page_list_type" :options="$page.props.enums.segment_page_list_types" optionValue="value" optionLabel="label" placeholder="Seçiniz" class="w-100" />
                    </div>
                </div>

                <Divider />

                <div class="d-flex flex-column mb-10">
                    <label class="form-label">Kısa Açıklama</label>
                    <TextEditor v-model="form.summary" />
                </div>

                <Divider />

                <div class="row g-5">
                    <div class="col-lg-4 d-flex flex-row">
                        <InputSwitch v-model="form.show_in_shop" />
                        <label class="fw-bold ms-2">Mağazada gösterilsin</label>
                    </div>
                    <div class="col-lg-4 d-flex flex-row">
                        <InputSwitch v-model="form.is_published" />
                        <label class="fw-bold ms-2">Yayınla</label>
                    </div>
                </div>
                <div class="separator my-10"></div>
                <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                <FormErrors :form="form"/>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import InputSwitch from 'primevue/inputswitch';
import Editor from 'primevue/editor';
import Dropdown from "primevue/dropdown";
import Divider from "primevue/divider";

const dialogVisible = ref(false);

const form = useForm({
    name:"",
    show_in_shop:false,
    is_published:false,
    summary:"",
    segment_page_list_type:""
});

const create = () => {
    form.post(route('product-segments.store'),{
        onSuccess: () => {
            form.reset();
            dialogVisible.value = false;
        },
    });
}

</script>